/*
* Accounting Partners Templates Archive styles
*/
body.unmoved {
  overflow: hidden;
}

header.header {
  position: relative;
  z-index: 3;
  background-color: #fff;
}

.accounting-partners-filter {
  position: relative;
  z-index: 5;
  margin: 0 -15px 30px;
}
@media (min-width: 768px) {
  .accounting-partners-filter {
    margin: -46px -15px 22px;
  }
}
@media (min-width: 1024px) {
  .accounting-partners-filter {
    margin: -46px -15px 40px;
  }
}
.accounting-partners-filter .br-8 {
