    position: relative;
    top: 0;
    right: auto;
    margin-top: 0;
  }
}
.accounting-partners-filter__button_location.opc-trans {
  opacity: 0;
  animation: back-in-opacity 0.4s forwards;
  animation-delay: 0.1s;
}
.accounting-partners-filter__button_location::after {
  top: 34px;
  right: 13px;
  display: inline-block;
  width: 11px;
  height: 12px;
  margin-left: 8px;
  vertical-align: middle;
  background-image: url(./../../../images/accounting-partners/tile-magnifier.svg);
