}
.accounting-partners-filter__button.selected:not(.active)::before {
  position: absolute;
  top: calc( 100% - 7px );
  left: 50%;
  z-index: 6;
  display: block;
  width: 4px;
  height: 4px;
  margin-left: -2px;
  background-color: #359ce4;
  border-radius: 50%;
  content: none;
}
@media (min-width: 768px) {
  .accounting-partners-filter__button.selected:not(.active)::before {
    content: "";
