  }
}
.accounting-partners-filter__button.active {
  position: relative;
  border: none;
}
@media (min-width: 768px) {
  .accounting-partners-filter__button.active {
    color: #0075dd;
  }
}
.accounting-partners-filter__button.active::before {
  position: absolute;
  top: calc( 100% - 1px );
  left: 50%;
  z-index: 6;
  display: block;
  width: 16px;
  height: 11px;
  margin-left: -8px;
  background-image: url(./../../../images/accounting-partners/arrow-image.svg);
  background-repeat: no-repeat;
  background-size: 16px 12px;
  content: none;
}
@media (min-width: 768px) {
  .accounting-partners-filter__button.active::before {
    top: calc( 100% - 9px );
    content: "";
  }
}
@media (min-width: 1024px) {
  .accounting-partners-filter__button.active::before {
    top: calc( 100% - 1px );
  }
}
.accounting-partners-filter__button.active::after {
  background-image: url(./../../../images/accounting-partners/tile-chevron-up.svg);
}
.accounting-partners-filter__location {
  position: relative;
  display: block;
  width: 100%;
  padding: 0 25px 14px;
  border-bottom: 1px solid rgba(180, 190, 198, 0.3);
}
