.accounting-partners-filter__location-submit {
  position: absolute;
  top: 50%;
  right: 40px;
  width: 16px;
  height: 16px;
  margin-top: -12px;
  background-color: transparent;
  background-image: url(./../../../images/accounting-partners/blue-magnifier.svg);
  background-repeat: no-repeat;
  background-size: 16px 16px;
  border: none;
  visibility: visible;
  cursor: default !important;
  opacity: 0;
  transition: none;
}
@media (min-width: 768px) {
  .accounting-partners-filter__location-submit {
    right: 10px;
    margin-top: -5px;
  }
}
.accounting-partners-filter__location-input, .accounting-partners-filter__location-input:focus {
  position: absolute;
  top: 1px;
  display: block;
  width: 0;
  height: 44px;
  margin-top: 14px;
  padding: 0 41px 0 15px;
  color: #002d79;
  font-size: 1.125rem;
  line-height: 1.5rem;
  border: 1px solid rgba(180, 190, 198, 0.6);
  border-radius: 8px 8px 0 0;
  outline: none;
  visibility: hidden;
  opacity: 0;
  transition: width 0.4s ease;
}
@media (min-width: 768px) {
  .accounting-partners-filter__location-input, .accounting-partners-filter__location-input:focus {
    position: relative;
    margin-top: 0;
  }
}
.accounting-partners-filter__location-input::-webkit-input-placeholder, .accounting-partners-filter__location-input:focus::-webkit-input-placeholder {
  opacity: 0.5;
}
.accounting-partners-filter__location-input::-moz-placeholder, .accounting-partners-filter__location-input:focus::-moz-placeholder {
  opacity: 0.5;
}
.accounting-partners-filter__location-input:-ms-input-placeholder, .accounting-partners-filter__location-input:focus:-ms-input-placeholder {
  opacity: 0.5;
}
.accounting-partners-filter__location-input:-moz-placeholder, .accounting-partners-filter__location-input:focus:-moz-placeholder {
  opacity: 0.5;
}
.accounting-partners-filter__location-input::-ms-clear, .accounting-partners-filter__location-input:focus::-ms-clear {
  display: none;
}
.accounting-partners-filter__location-input.active, .accounting-partners-filter__location-input:focus.active {
  position: relative;
  width: 100%;
  visibility: visible;
  opacity: 1;
}
.accounting-partners-filter__location-input.active + .accounting-partners-filter__location-submit, .accounting-partners-filter__location-input:focus.active + .accounting-partners-filter__location-submit {
  visibility: visible;
  opacity: 1;
  transition: opacity 0.4s ease;
  transition-delay: 0.4s;
}
.accounting-partners-filter__location-input:placeholder-shown {
  border-radius: 8px;
}
.accounting-partners-filter__label {
